body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
        "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4,
.outOf {
    text-align: center;
}

.App {
    margin: 0 auto;
    max-width: 500px;
}
.question {
}

.correct {
    background-color: #93d993 !important;
}
.false {
    background-color: #db7272 !important;
}
.parsedText {
    img {
        max-width: 100%;
    }
}
.spinner {
    height: 100px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.answers {
    display: flex;
    align-items: center;

    img {
        max-height: 200px;
        max-width: 100%;
    }
}
.result {
    .count {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .correctCount {
        color: green;
        font-size: 2rem;
        margin-right: 0.5rem;
    }
    .falseCount {
        font-size: 2rem;
        margin-right: 0.5rem;
        color: red;
    }
    .percentage {
        display: flex;
        flex-direction: column;
        align-items: center;
        .percentageNumber {
            font-size: 4rem;
        }
        margin-bottom: 2rem;
    }
    .good {
        color: green;
    }
    .bad {
        color: red;
    }
    .restultQuestion {
        margin: 0.5rem 0;
    }
}
.MuiPaper-elevation3 {
    margin: 0.5rem 0;
    padding: 0.5rem;
}
h2 {
    margin: 0.25rem;
}
.questionNavi {
    display: flex;
    justify-content: flex-end;
    button {
        margin-left: 1rem;
    }
}
.fileUpload {
    margin: 1rem 0;
    .name {
        margin: 0.25rem;
    }
}
.settings {
    margin: 1rem;
    display: flex;
    flex-flow: column;
    > div {
        margin: 1rem 0 !important;
    }
}
.startSettings {
    display: flex;
    flex-flow: column;
    > div {
        margin: 1rem !important;
    }
}
.stepper {
    display: flex;
    justify-content: space-between;
    align-items: center;

    input::-webkit-outer-spin-button,
    input::-webkit-inner-spin-button {
        -webkit-appearance: none;
        margin: 0;
    }
    input[type="number"] {
        -moz-appearance: textfield;
        border-top: none;
        border-left: none;
        border-right: none;
        max-width: 2.5rem;
        font-size: 1rem;
        text-align: center;
    }
}
.resultButton {
    margin: 0.5rem 0;
    .MuiPaper-elevation3 {
        padding: 0;
    }
    * {
        width: 100%;
    }
}
.questionsetSelect {
    display: flex;
    justify-content: space-between;
    width: 100%;
    svg {
        height: 20px;
    }
}
.questionEditor {
    position: relative;

    .addNewQuestion {
        position: fixed;
        bottom: 3rem;
        right: 3rem;
    }
}
